/*********============== GLOBAL STYLES ==============*********/
/*** Box Sizing ***/
html {
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  font-size: 100%;
  height: 100%;
}

*,
*:after,
*:before {
  -moz-box-sizing: inherit;
       box-sizing: inherit;
}
/******================ FONTS & MARGINS =================******/
body {
  background-color: #ffffff;
  font-family: 'Source Sans Pro', 'Avenir Next', 'SegoeUI', 'Arial', sans-serif;
  font-size: 14px;
  font-size: -webkit-calc(14px + 7 * ((100vw - 380px) / 760));
  font-size: calc(14px + 7 * ((100vw - 380px) / 760));
  line-height: 1.6;
  min-height: 100%;
  position: relative;
  padding-bottom: 70px;
}
@media screen and (min-width: 1140px) {
  body {
    font-size: 21px;
  }
}
@media screen and (max-width: 380px) {
  body {
    font-size: 14px;
  }
}
/*** Paragraph ***/
p {
  font-size: 1em;
  max-width: 36em;
  color: #111111;
  margin: 0;
  -webkit-hyphens: auto;
     -moz-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}
/*** Heading ***/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Oxygen', 'Avenir Next', 'SegoeUI', 'Arial', sans-serif;
  margin: 21px 0 0;
  /* non-scalable fallback for old browsers */
  margin-top: 1.5rem;
  /* 21px, will be ignored by old browsers */
}
/*** Link Styles ***/
a {
  text-decoration: none;
}

a:link {
  color: #ff6732;
}

a:visited {
  color: #ff6732;
}

a:focus,
a:hover {
  border-bottom: 1px solid #ff6732;
}

a:active {
  color: #ff9d00;
}
/***** Images *****/
img {
  width: auto;
  max-width: 100%;
  /* max-width allows image to be responsive. */
  height: auto;
}
/*** Reset margin & padding to 0. ***/
header,
main,
section {
  margin: 1.5rem 0 0;
  padding: 0;
}
/**** Wrapper Div ****/
.wrap {
  width: 95%;
  max-width: 71.25em;
  margin: 0 auto;
}

li,
ul {
  margin: 0;
  padding: 0;
}
/***** Mobile styles *****/
/*********====== HEADER ======**********/
header h1 {
  padding: 0;
}

header h1 a {
  display: block;
  width: 100%;
  text-align: center;
}

header h1 a:link {
  color: #000;
}

header h1 a:visited {
  color: #000;
}

header h1 a:focus,
header h1 a:hover {
  border-bottom: none;
  color: #3b423c;
}

header h1 a:active {
  color: #0f1314;
}
/******===== HEADER - Navigation =====******/
nav {
  margin-top: 1rem;
  border: 1px solid #c8bc9d;
}

nav a {
  display: inline-block;
  font-size: 1.1em;
  width: 50%;
  text-transform: uppercase;
  text-align: center;
  padding: 1em 0;
  margin: 0;
  letter-spacing: 0.12em;
}

nav a:link {
  color: #ff6732;
}

nav a:visited {
  color: #ff6732;
}

nav a:focus,
nav a:hover {
  border-bottom: none;
  background-color: #f9d475;
  color: #000000;
}
/**********============ SECTION - about ===========***********/
.avatar,
.bio {
  margin: 1.5rem 0 0;
  padding: 0;
  width: 100%;
}

.avatar div {
  width: 130px;
  height: 130px;
  margin: 0 auto;
}

.bio > p {
  max-width: 36em;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.bio > * + * {
  margin-top: 1.1rem;
}

/**********============ SECTION - social ===========***********/
.social {
  display: none;
  margin-top: 2rem;
}

.social-m {
  margin: 1.5rem 0 0;
  padding: 1em 0;
  width: 100%;
  border: 1px solid #c8bc9d;
}

.social-m a {
  display: inline-block;
  margin: 0;
  padding: 0.1em;
  width: 24%;
  text-align: center;
  border-right: 1px solid #c8bc9d;
}

.social-m a:last-child {
  border-right: none;
}

.social-m a:link {
  color: #ff6732;
}

.social-m a:visited {
  color: #ff6732;
}

.social-m a:hover,
.social-m a:focus {
  color: #94f39f;
  border-bottom: none;
}

main {
  padding-bottom: 1.5rem;
}
/************========= FOOTER ==========*************/
footer {
  margin: 0;
  padding: 0;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 70px;
  background-color: none;
}

footer > p + p {
  margin-top: 0.5em;
}

.half,
.one,
.one-fourth,
.one-third,
.two-thirds {
  width: 100%;
}
/* Blog Page Styles */
.blog-wrap {
  max-width: 42em;
  margin: 0 auto;
}

.blog-main {
  margin-top: 3rem;
}

.blog-list {
  list-style: none;
  padding: 1rem;
  max-width: 40em;
  margin: 0 auto;
}

.blog-list li {
  font-size: 1.2em;
  text-align: center;
}

.post-wrap {
  max-width: 40em;
  margin: 0 auto 100px;
}
/*** Link Styles ***/
.post-wrap a:link {
  color: #ff6732;
}

.post-wrap a:visited {
  color: #ff6732;
}

.post-wrap a:focus,
.post-wrap a:hover {
  border-bottom: 2px solid #ff6732;
}

.post-wrap a:active {
  color: #ff9d00;
}

.post-article {
  max-width: 45em;
  margin: 4rem auto 0;
  line-height: 1.8;
  padding: 0 0.25rem;
}

.post-article p {
  max-width: 100%;
}

.post-date {
  font-size: 0.75em;
  margin: 0.5rem 0 1.5rem;
}
/* Introduces Vertical Rhythm.
* Whitespace above subhead is larger than whitespace below subhead
*/
.post-article p + h2 {
  margin-top: 2.1rem;
}

.post-article h2 + p {
  margin-top: 0.7rem;
}

.post-article p + h3 {
  margin-top: 2.1rem;
}

.post-article h3 + p {
  margin-top: 0.7rem;
}

.post-article p + h4 {
  margin-top: 2.1rem;
}

.post-article h4 + p {
  margin-top: 0.7rem;
}
/* Introduces Vertical Rhythm.
* Whitespace above list items.
*/
.post-article ul,
li {
  margin-top: 1.05rem;
  padding: 0;
}

.post-article ul p,
li p {
  margin: 0;
  padding: 0;
}

.post-article ul {
  margin-left: 1rem;
  list-style-position: outside;
}

.post-article li + li {
  margin-top: 1.5rem;
}

.post-article p + p {
  margin-top: 1rem;
}

.post-article ul {
  margin-left: 2.5rem;
}

.post-article ul + p {
  margin-top: 0.8rem;
}
/* Heading Colors */
.post-wrap h1,
h2,
h3,
h4,
h5,
h6 {
  color: #079FC0; /*#0056a8;*/
}

/* Blockquote */
.post-article blockquote {
  font-style: italic;
  margin: 1.5em 0;
  border-left: 2px solid #ff6732;
  padding: .75em 0 0 1.5em;
}

 /* Pre */
 pre {
  display: block;
  padding: 9.5px;
  margin: 0 0 10px;
  font-size: 1em;
  line-height: 1.5;
  color: #333;
  word-break: break-all;
  word-wrap: break-word;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* code */
code {
  font-family: monospace;
  padding: 2px 4px;
  font-size: 90%;
  color: #333;
  border-radius: 4px;
  /*background-color: #eff0f1;*/
}

.post-article .highlighter-rouge {
  font-size: 0.75em;
  padding: 0.1em 0.4em;
  margin: 0 0.2em;
  background-color: #fafafa;
  color: rgb(0, 0, 0);
  box-shadow: 0px 1px 2px #888888;
}

.post-article div[class^="language-"] {
  background-color: #ffffff;
  box-shadow: none;
  border: 0;
  padding: 0;
  margin-bottom: 1em;
  margin-top: 1em;
}


.highlight {
  box-shadow: inset 0 1px 0 #fff, 1px 1px 0 #fff, 2px 2px 0 #ddd, 3px 3px 0 #fff, 4px 4px 0 #ddd;
  border-radius: 4px;
}
/* Following code will remove semicolon highlighting. */
.highlight .err {
  background-color: transparent;
}

.post-article pre.highlight, .post-article .highlight pre {
  overflow-x: auto;
}

.post-article .highlight pre code, .post-article pre.highlight code {
  overflow-wrap: normal;
  white-space: pre;
}

/**** Horizontal Rule ****/
.line {
  margin-top: 40px;
  margin-bottom: 40px;
  width: 250px;
}
/**** Disqus styles ****/
.disqus {
  margin-top: 100px;
  max-width: 45em;
  margin: 0 auto;
}


/**************======= TAB styles ========***************/
@media screen and (min-width: 40em) {
  /**** Wrapper Div ****/
  .wrap {
    width: 95%;
    max-width: 60em;
    margin: 0 auto;
  }
  /**** Disqus styles ****/
  .disqus {
    margin-top: 100px;
  }
  .one {
    width: 100%;
  }

  .half {
    width: -webkit-calc(100% / 2);
    width: calc(100% / 2);
  }

  .one-third {
    width: -webkit-calc(100% / 3);
    width: calc(100% / 3);
  }

  .one-fourth {
    width: -webkit-calc(100% / 4);
    width: calc(100% / 4);
  }

  .two-thirds {
    width: -webkit-calc(100% / 3 * 2);
    width: calc(100% / 3 * 2);
  }

  .column {
    float: left;
  }
  /* CLEARFIX */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  /*********** Header **********/
  header {
    margin: 0;
    padding: 1rem 0;
    border-bottom: 2px solid #c8bc9d;
  }

  header h1 {
    margin: 0;
    padding: 0;
    font-size: 2em;
    height: 40px;
  }

  header h1 a {
    display: block;
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }

  header h1 a:link {
    color: #000;
  }

  header h1 a:visited {
    color: #000;
  }

  header h1 a:focus,
  header h1 a:hover {
    color: #3b423c;
  }

  header h1 a:active {
    color: #0f1314;
  }
  /******===== HEADER - Navigation =====******/
  nav {
    margin: 0;
    padding: 0;
    height: 40px;
    line-height: 40px;
    border: none;
  }

  .nav {
    max-width: 18.75em;
    margin: 0 auto;
  }

  nav a {
    display: inline-block;
    font-size: 1.1em;
    width: 50%;
    text-transform: uppercase;
    text-align: center;
    padding: 0;
    margin: 0;
    letter-spacing: 0.12em;
  }

  nav a:link {
    color: #ff6732;
  }

  nav a:visited {
    color: #ff6732;
  }

  nav a:focus,
  nav a:hover {
    background-color: #fff;
  }
  /******* MAIN ********/
  main {
    margin-top: 5rem;
  }
  /**********============ SECTION - about ===========***********/
  .avatar,
  .bio {
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .bio {
    margin-top: 24px;
    margin-top: 2rem;
  }

  .avatar div {
    width: 130px;
    height: 130px;
    margin: 0 auto;
  }

  .bio > p {
    max-width: 36em;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }

  .bio > * + * {
    margin-top: 1.1rem;
  }

  /*============ SECTION - social ===========*/
  .social-m {
    margin: 2rem 0 0;
    padding: 1em 0;
    max-width: 22em;
    margin: 0 auto;
    border: none;
  }

  /************========= FOOTER ==========*************/
  footer p {
    text-align: center;
    margin: 0 auto;
  }

  footer small {
    font-size: 65%;
  }
}

/************========= DESKTOP =========************/
